.loginButton {
	background-color: rgb(36, 36, 255);
	color: white;
	border-radius: 15px;
	padding: 10px;
	font-size: 3.5em;
	margin-top: 300px;
	-webkit-box-shadow: 2px 0px 50px;
	        box-shadow: 2px 0px 50px;
}

.logo {
	color: rgb(160, 0, 120);
}

.overlay1,
.overlay2 {
	margin-top: 20%;
	height: 700px;
	width: 465px;
	margin: auto;
	border-radius: 20px;
	-webkit-box-shadow: 2px 0px 50px;
	        box-shadow: 2px 0px 50px;
}

.messageTitle {
	text-align: left;
	padding-top: 25px;
	padding-left: 45px;
	color: darkslategrey;
	font-size: 1.6em;
}

.message {
	text-align: left;
	padding-left: 45px;
	color: grey;
	padding-top: 0px;
	margin-top: -10px;
	padding-bottom: 7%;
}

.mobile,
.enterOtp, .email {
	width: 360px;
	display: block;
	margin-left: 45px;
	height: 30px;
	border-radius: 10px;
	padding-left: 10px;
	font-size: 1.1em;
	font-weight: 600;
}

.mobileTitle,
.otpTitle,
.emailTitle {
	margin-top: 30px;
	text-align: left;
	margin-left: 45px;
}

.done1 {
	margin-top: 59%;
	margin-left: 70%;
	border-radius: 11px;
	background-color: #2600f7;
	color: white;
	width: 70px;
	height: 45px;
}

.done2 {
	margin-top: 13%;
	margin-left: 70%;
	border-radius: 11px;
	background-color: #2600f7;
	color: white;
	width: 70px;
	height: 45px;
}

.or {
	font-weight: bolder;
	font-size: 1.2em;
}

.popUp {
	width: 250px;
	height: 250px;
	vertical-align: middle;
	border-radius: 20px;
	-webkit-box-shadow: 1px 1px 1px 1px;
	        box-shadow: 1px 1px 1px 1px;
	-webkit-box-shadow: 2px 0px 50px;
	        box-shadow: 2px 0px 50px;
	background-color: white;

	margin-top: -500px;
	margin-left: 700px;
}

.error{
    text-align: left;
    color: red;
    margin: 0px;
    padding: 0px;
	margin-left: 45px;
	display: block;
}


